.projects-section {
  padding: 4rem 4rem;
  z-index: 3;         // Ensure it scrolls OVER the About section
  background-color: var(--background-color);


  .project-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    width: 100%;
    min-height: 100vh;
    height: 100vh;
    align-items: stretch;
  }

  .project {
    display: flex;
    flex-direction: column;
    background: #fff;
    overflow: hidden;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    text-decoration: none;
    color: var(--primary-text-color);
    transition: box-shadow 0.3s;
  }
  .project:focus {
    outline: 2px solid var(--accent-color);
    outline-offset: 2px;
  }
  .img-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    aspect-ratio: 3 / 5;
    display: flex;
    align-items: stretch;
    justify-content: center;
    background: #eee;
    overflow: hidden;
  }
  .img-wrapper .img {
    position: absolute;
    top: 0; left: 0; width: 100%; height: 100%;
    object-fit: cover;
    display: block;
    transition: opacity 0.4s cubic-bezier(0.25,0.46,0.45,0.94);
    pointer-events: none;
  }
  .img-wrapper .imgBlurred {
    z-index: 1;
    filter: blur(16px) brightness(0.9);
    opacity: 1;
  }
  .img-wrapper .normalImg {
    z-index: 2;
    opacity: 1;
  }
  .website-wrapper {
    position: absolute;
    bottom: 0; left: 0; width: 100%;
    z-index: 3;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .website-wrapper img {
    width: 80%;
    max-width: 320px;
    box-shadow: 0 4px 24px rgba(0,0,0,0.12);
    border-radius: 8px;
  }
  .title {
    font-family: 'Switzer', Helvetica, Arial, sans-serif;
    font-size: 2rem;
    margin-left: -4rem;
    font-weight: 700;
    color: var(--primary-text-color);
    margin: 1.5rem 0 0.5rem 0;
    line-height: 1.1;
    display: flex;
  }
  .project > span {
    font-family: 'Switzer', Helvetica, Arial, sans-serif;
    font-size: 1.15rem;
    color: var(--primary-text-light);
    font-weight: 500;
    margin-bottom: 0;
    display: block;
  }

  /* Basic Responsive */
  @media (max-width: 900px) {
    .project-grid {
      grid-template-columns: 1fr;
      gap: 2rem;
    }
    .project {
      min-height: 400px;
      height: auto;
      max-height: none;
    }
    .img-wrapper {
      min-height: 220px;
    }
  }
}

.footer {
  position: relative;
  width: 100vw;
  min-height: 220px;
  background: var(--background-color, #111);
  overflow: hidden;
  padding: 0;
}

.footer-social {
  position: relative;
  z-index: 2;
  display: flex;
  gap: 2.5rem;
  align-items: flex-start;
  padding: 2.5rem 0 1.5rem 4rem;
  font-size: 1.25rem;
  font-family: 'Switzer', Helvetica, Arial, sans-serif;
  color: #fff;
  a {
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    opacity: 0.85;
    transition: opacity 0.2s;
    &:hover { opacity: 1; }
  }
}

.footer-logo-scroll {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 260px;
  overflow: hidden;
  z-index: 1;
}

.footer-logo-img {
  height: 300px;
  min-width: 120vw;
  width: auto;
  fill: #fff;
  color: #fff;
  opacity: 0.12;
  filter: brightness(10) invert(1);
  pointer-events: none;
  user-select: none;
  position: absolute;
  left: 0;
  bottom: -60px;
  will-change: transform;
} 